
import styles from '../Modal.module.scss';
import { AiFillCloseCircle } from 'react-icons/ai'
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { useState } from 'react';
import {LoadingButton} from '@mui/lab'
import { Title } from 'src/components/Title/Title';
import { useEffect } from 'react';
import { TextField } from '@mui/material';
import http from 'src/http';
import { IBrand } from 'src/models/product';


export const BrandsModal = ({ openModal, setModal, data, edit}: ModalPropsTestEdit) => {

  const [editedBrand, setEditedBrand] = useState<string>('');
  const [newBrand, setNewBrand] = useState<string>('');
  const [idBrand, setIdBrand] = useState<string>('');
  const [loading, setLoading] = useState<Boolean>(false);
  const [message, setMessage] = useState<string>('');


  async function editBrandById(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.put<IBrand>(`/marcas/${idBrand}`, {
      nome: editedBrand,
      identificador: '???'
    })
      .then(response=>{
          if(response.status == 202){
            setMessage("Marca editada com sucesso")
          }
      }).catch(error=>
        setMessage("Erro no servidor, por favor, tente novamente.")
      )
    setLoading(false);
    window.location.reload();
  }


  async function createBrand(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.post<IBrand>('/marcas', {
      nome: newBrand,
      identificador: '???'
    })
      .then(response=>{
          if(response.status == 202){
            setMessage("Marca criada com sucesso")
          }
      }).catch(error=>
        setMessage("Erro no servidor, por favor, tente novamente.")
      )
    setLoading(false);
    window.location.reload();
  }


  useEffect(()=>{
    if(data){
      setEditedBrand(data.nome);
      setIdBrand(data.id);
    }
  }, [data]);



  return (
    <>
      {openModal && (
        <div className={styles.modalCategory} style={{backgroundColor: 'rgb(255, 255, 255)'}}>
          <div className={styles.modal__divCloseButton}>
            <button
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e) => setModal(!openModal)}
            >
              <AiFillCloseCircle />
            </button>
          </div>

          <Title>Nova marca</Title>
          <form onSubmit={edit? editBrandById: createBrand}>
            <div style={{ marginTop: '1rem' }}>
              <TextField 
                id="brand_name"
                type="text"
                label="Nome"
                fullWidth={true}
                required={true}
                color="primary"
                placeholder="Digite o nome da nova marca"
                onChange={edit? (e) => setEditedBrand(e.target.value): (e)=>setNewBrand(e.target.value)}
                value={edit? editedBrand: newBrand}
              />
            </div>


            <div style={{ display: 'flex', gridGap: '1rem', marginTop: '1rem'}}>
              {edit ? 
                <LoadingButton 
                  color='primary' 
                  type='submit' 
                  variant='contained' 
                  size='medium'
                  loading={loading?true: false}
                >
                  Editar
                </LoadingButton>
                  :
                <LoadingButton 
                  color='primary' 
                  type='submit'
                  variant='contained' 
                  size='medium'
                  loading={loading?true: false}
                >
                  Cadastrar
                </LoadingButton>
              }
              <LoadingButton 
                color='warning' 
                size='medium'
                variant='contained' 
                onClick={(e)=>setModal(!openModal)}
                >
                  Cancelar
              </LoadingButton>
            </div>
            {!loading && message &&
              <p>{message}</p>
            }
            
          </form>

        </div>
      )}
    </>
  )
}
