const applyFilters = (
  brands: IBrand[],
): IBrand[] => {
  return brands.filter((brands) => {
    let matches = true;
    return matches;
  });
};

const applyPagination = (
  brands: IBrand[],
  page: number,
  limit: number
): IBrand[] => {
  return brands.slice(page * limit, page * limit + limit);
};

