import { Card } from '@mui/material';
import { useEffect, useState } from 'react';
import http from 'src/http/index';
import { IBrand } from 'src/models/product';
import BrandsTable from './BannersTable';



function Banners() {

  const [brands, setBrands] = useState<IBrand[]>([]);

  async function getAllBrands(){
    const {data} = await http.get("/marcas");
    setBrands(data);
  }

  useEffect(()=>{
    getAllBrands();
  }, [])

  return (
    <Card>
      <BrandsTable brands={brands} panel={false}/>
    </Card>
  );
}

export default Banners;
